import { Swiper } from "../_vendor";

const slider = () => {
  // Card Slider

  const cardSlider = document.querySelector(".swiper-card");
  let swiperCard;

  function cardSwiper() {
    if (window.innerWidth <= 768 && cardSlider.dataset.card == "false") {
      swiperCard = new Swiper(cardSlider, {
        slidesPerView: "auto",
        spaceBetween: 10,
      });

      cardSlider.dataset.card = "true";
    }
  }

  cardSwiper();

  window.addEventListener("resize", () => {
    cardSwiper();
  });

  // Product Slider Cat

  const productSliderCat = document.querySelector(".product-swiper-cat");
  let mySwiperCat;

  function productSwiperCat() {
    if (
      window.innerWidth <= 768 &&
      productSliderCat.dataset.mobile == "false"
    ) {
      mySwiperCat = new Swiper(productSliderCat, {
        slidesPerView: "auto",
        spaceBetween: 10,

        breakpoints: {
          425: {
            centeredSlides: false,
          },
          320: {
            slidesPerView: "auto",
            centeredSlides: true,
          },
        },
      });

      productSliderCat.dataset.mobile = "true";
    }

    if (window.innerWidth > 768) {
      productSliderCat.dataset.mobile = "false";
      if (productSliderCat.classList.contains("swiper-initialized")) {
        mySwiperCat.destroy();
      }
    }
  }
  productSwiperCat();

  window.addEventListener("resize", () => {
    productSwiperCat();
  });

  const productSliderDog = document.querySelector(".product-swiper-dog");
  let mySwiperDog;

  function productSwiperDog() {
    if (
      window.innerWidth <= 768 &&
      productSliderDog.dataset.mobile == "false"
    ) {
      mySwiperDog = new Swiper(productSliderDog, {
        slidesPerView: "auto",
        spaceBetween: 10,

        breakpoints: {
          425: {
            centeredSlides: false,
          },
          320: {
            slidesPerView: "auto",
            centeredSlides: true,
          },
        },
      });

      productSliderDog.dataset.mobile = "true";
    }

    if (window.innerWidth > 768) {
      productSliderDog.dataset.mobile = "false";
      if (productSliderDog.classList.contains("swiper-initialized")) {
        mySwiperDog.destroy();
      }
    }
  }
  productSwiperDog();

  window.addEventListener("resize", () => {
    productSwiperDog();
  });

  // news

  const newsSwiper = new Swiper(".news-slider", {
    slidesPerView: "auto",
    spaceBetween: 30,

    pagination: {
      el: ".swiper-pagination",
      clickable: true,
    },

    breakpoints: {
      768: {
        spaceBetween: 16,
      },
    },
  });

  // reviews

  new Swiper(".reviews-mobile", {
    slidesPerView: "auto",
    spaceBetween: 30,

    breakpoints: {
      768: {
        spaceBetween: 16,
      },
      425: {
        spaceBetween: 10,
      },
    },
  });
};

export { slider };
